<template>
  <div>
    <h3>Add Item</h3>
    <div>
      <form @submit.prevent="saveItem" class="form-control">
        <input type="text" v-model="item_id" required class="form-control" />
        <label>Item Id</label>
        <input type="text" v-model="item_name" required class="form-control" />
        <label>Item Name</label>
        <input type="text" v-model="item_price" required class="form-control" />
        <label>Item Price</label>
        <br />
        <button type="submit" class="btn btn-primary">Submit</button>
        <router-link
          v-bind:to="{
            name: 'admin',
          }"
          class="btn btn-warning"
        >
          Cancel
        </router-link>
      </form>
    </div>
  </div>
</template>

<script>
import db from "@/firebaseConfig.js";
export default {
  name: "new-item",
  data() {
    return {
      item_id: null,
      item_name: null,
      item_price: null,
    };
  },
  methods: {
    saveItem() {
      db.collection("categories")
        .doc(this.$route.params.cat_id)
        .collection("items")
        .add({
          item_id: this.item_id,
          item_name: this.item_name,
          item_price: this.item_price,
        })
        .then((docRef) => this.$router.push("/admin"))
        .catch((error) => console.log(err));
    },
  },
};
</script>
